import CommonNav from "@/components/CommonNav.vue";
import CommonFooter from "@/components/CommonFooter.vue";
export default {
  name: "ContactConsult",

  data() {
    return {
      formMess: {
        name: "",
        phone: "",
        value: "立即咨询"
      }
    };
  },

  components: {
    CommonNav,
    CommonFooter
  },
  methods: {
    controlRadio($event) {
      let that = this;
      window.setTimeout(() => {
        if (!this.changed) {
          $event.target.checked = false;
        }

        that.changed = false;
      }, 0);
    },

    retainRecord() {
      this.changed = true;
    },

    onsubmit($event) {
      this.disabled = true;
      this.$refs.submitinput.value = "已提交";
    }

  },

  mounted() {// console.log(this.$refs.submitinput.value);
  }

};